export class FunctionArgs {
    constructor() {
        this._parameters = [];
    }
    get Result() {
        return this._result;
    }
    set Result(value) {
        this._result = value;
        this.HasResult = true;
    }
    get Parameters() {
        return this._parameters;
    }
    set Parameters(value) {
        this._parameters = value;
    }
}
